html {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 65%;
  margin: 0 auto;

  color: #fff;

  background: linear-gradient(107.56deg, #191536 0%, #000000 100%);
  border: 10px solid rgb(0, 0, 0);

  border-radius: 40px;

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    font-size: 10px;

    padding: 40px 8px;
  }

  &__list {
    width: 80%;
    padding: 0;
  }

  &__clear {
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: #ffffff;
    border-radius: 4px;
    border: 0;
    box-shadow: rgba(138, 138, 138, 0.5) 0 -1px 3px 0 inset,rgba(0,44,97,.1) 0 3px 6px 0;
    box-sizing: border-box;
    color: #000;
    display: inherit;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    margin: 0;
    height: 40px;
    width: 100px;
    padding: 16px 20px;
    transition: all .2s cubic-bezier(.22, .61, .36, 1);

    cursor: pointer;

    &:hover {
      background-color: #c5c5c5;
    }
  }
}

.Good {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
  padding: 8px;
  border-radius: 10px;
  transition: background-color 0.3s;
  font-weight: 700;

  &--active {
    background-color: rgba(79, 78, 78, 0.261);
  }

  &__select, &__remove {
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: #0ad428;
    border-radius: 4px;
    border: 0;
    box-shadow: rgba(16, 235, 20, 0.5) 0 -1px 3px 0 inset,rgba(0,44,97,.1) 0 3px 6px 0;
    box-sizing: border-box;
    color: #fff;
    display: inherit;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    margin: 0;
    height: 40px;
    width: 100px;
    padding: 16px 20px;
    transition: all .2s cubic-bezier(.22, .61, .36, 1);

    cursor: pointer;

    &:hover {
      background-color: #25f73a;
    }
  }

  &__remove {
    background-color: #e50707;
    box-shadow: rgba(93, 6, 6, 0.5) 0 -1px 3px 0 inset,rgba(0,44,97,.1) 0 3px 6px 0;

    &:hover {
      background-color: #e51313;
    }
  }
}
